.header {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	z-index: 100;
	padding-top: 25px;
	padding-bottom: 10px;
}
.header__wrapper {
	display: flex;
	align-items: center;
}
.socials-wrapper {
	margin-left: auto;
}

.menu-burger {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 54px;
	height: 54px;
	background-color: #612b1c;
	margin-left: 56px;
	margin-right: 64px;
}
.burger {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 16px;
	cursor: pointer;
}
.burger__line {
	width: 22px;
	height: 2px;
	background-color: #ffffff;
}

@media (max-width: 576px) {
	.header__wrapper {
		padding-top: 25px;
		padding-bottom: 25px;
	}
}
