.social {
	display: flex;
	align-items: center;
}

.social-item {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 27px;
	height: 27px;
	background-color: #612b1c;
	border-radius: 50%;
	transition: background-color 0.3s ease;
}
.social-item:not(:last-child) {
	margin-right: 10px;
}

.social-link {
	width: 17px;
	height: 17px;
}

.social-icon {
	width: 100%;
	height: 100%;
	fill: #fff06a;
	transition: fill 0.3s ease;
}

.social-item:hover {
	background-color: #fff06a;

	& .social-icon {
		fill: #612b1c;
	}
}
