.welcome {
	position: relative;
	z-index: 12px;

	&::after {
		position: absolute;
		left: 15%;
		bottom: 0;
		z-index: 1;
		content: "";
		width: 274px;
		height: 100%;
		background-image: url("../img/vslid.png");
		background-position: left center;
		background-size: cover;
		background-repeat: no-repeat;
	}
}
.welcome__top {
	background-color: #fff06a;
	padding-top: 110px;
	padding-bottom: 80px;
}
.welcome__top .welcome__inner {
	max-width: 660px;
	margin-left: auto;
}
.title {
	font-family: RobotoSlab;
	font-weight: 700;
	font-size: 60px;
	color: #612b1c;
}
.welcome .title {
	margin-bottom: 30px;
}
.welcome__quote {
	font-family: RobotoSlab;
	font-style: normal;
	font-weight: 400;
	color: #612b1c;
	text-align: left;
	position: relative;
	padding-left: 25px;
	&::after {
		position: absolute;
		top: -10px;
		left: -15px;
		width: 25px;
		height: 28px;
		content: "";
		background-image: url("../img/quo.svg");
		background-position: left center;
		background-size: cover;
		background-repeat: no-repeat;
	}
}
.welcome__quote p {
	font-size: 12px;
	line-height: 15px;
}

.welcome__quote figcaption {
	font-size: 9px;
	line-height: 12px;
	margin-top: 7px;
}

.welcome__btn {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 386px;
	height: 56px;
	border: 2px solid transparent;
	background-color: #f82e2c;
	border-radius: 60px;
	margin-top: 85px;
	transition: all 0.3s ease;

	span {
		font-family: Montserrat;
		font-style: normal;
		font-weight: 400;
		font-size: 18px;
		line-height: 22px;
		text-align: center;
		color: #ffffff;
		transition: color 0.3s ease;
	}
}
.welcome__btn:hover {
	border: 2px solid #f82e2c;
	background-color: #fff;
	span {
		color: #f82e2c;
	}
}
.btn-icon {
	width: 41px;
	height: 28px;
	fill: #fff;
	margin-right: 23px;
	transition: fill 0.3s ease;
}
.welcome__btn:hover .btn-icon {
	fill: #f82e2c;
}
.welcome__bottom {
	padding-top: 100px;
	padding-bottom: 90px;
	background-color: #612b1c;

	& .container {
		max-width: 1140px;
	}
	& .welcome__inner {
		display: flex;
	}
}

.welcome__menu {
	display: flex;
	align-items: center;
}
.menu {
	margin-left: auto;
}
.menu__item:not(:last-child) {
	margin-right: 55px;
}
.menu__item-link {
	display: flex;
	align-items: center;
	text-decoration: none;

	img {
		margin-right: 15px;
	}

	span {
		font-family: Montserrat;
		font-style: normal;
		font-weight: 400;
		font-size: 16px;
		color: #ffffff;
	}
}

.menu__item-link:hover span {
	color: #fff06a;
}

@media (max-width: 1600px) {
	.welcome {
		&::after {
			left: 0;
		}
	}
}

@media (max-width: 1200px) {
	.welcome {
		&::after {
			display: none;
		}
	}
	.welcome__top .welcome__inner {
		margin-left: 0;
	}
	.menu {
		margin-left: 0;
		margin: 0 auto;
	}
}

@media (max-width: 998px) {
	.welcome__top .welcome__inner {
		margin: 0 auto;
	}
	.welcome__bottom,
	.worker {
		padding-top: 40px;
		padding-bottom: 40px;
	}
}

@media (max-width: 768px) {
	.welcome__menu {
		display: grid;
		grid-template-columns: 1fr 1fr;
		grid-gap: 30px;
	}
	.menu__item:not(:last-child) {
		margin-right: 0;
	}
}

@media (max-width: 576px) {
	.menu-burger {
		margin-left: 20px;
		margin-right: 0;
	}
	.title {
		font-size: 42px;
	}
	.header__wrapper {
		padding-top: 0px;
		padding-bottom: 25px;
	}
	.welcome__btn {
		max-width: 386px;
		width: 100%;
	}
}

@media (max-width: 380px) {
	.header__wrapper {
		flex-wrap: wrap;
		padding-bottom: 0;
	}
	.menu-burger {
		margin-left: 0px;
		margin-right: 0;
		margin-top: 20px;
	}
	.welcome__top {
		padding-top: 136px;
	}
	.welcome__btn {
		max-width: 386px;
	}
	.welcome__btn span {
		max-width: 155px;
	}
	.welcome__menu {
		grid-template-columns: 1fr;
	}
	.menu__item-link {
		justify-content: space-between;
	}
}
