.footer {
	background-color: #fff06a;
	padding-top: 55px;
	padding-bottom: 55px;
}
.footer__wrapper {
	display: flex;
	align-items: center;
}
.copyright {
	font-family: RobotoSlab;
	font-style: normal;
	font-weight: 400;
	font-size: 43px;
	line-height: 1.2;
	text-align: left;
	text-transform: uppercase;
	color: #612b1c;
	margin-right: 130px;

	span {
		background-color: #612b1c;
		color: #fff06a;
	}
}
.footer__menu {
	font-weight: 400;
	font-size: 16px;
	line-height: 20px;
	text-align: left;
	color: #612b1c;

	ul li:not(:last-child) {
		margin-bottom: 18px;
	}
}

.footer__contacts {
	display: flex;
	flex-direction: column;
	margin-left: auto;
}
.contact__link {
	font-size: 16px;
	line-height: 17px;
	color: #612b1c;
	margin-bottom: 10px;
}
.contact__link:hover,
.footer__menu a:hover {
	text-decoration: underline;
}
.footer__socials {
	margin-top: 25px;
}

@media (max-width: 768px) {
	.footer__wrapper {
		flex-direction: column;
		align-items: flex-start;
		max-width: 380px;
	}
	.copyright {
		margin-right: 0;
		margin-bottom: 20px;
	}
	.footer__menu {
		margin-bottom: 20px;
		ul li:not(:last-child) {
			margin-bottom: 10px;
		}
	}
	.footer__contacts {
		margin-left: 0;
	}
}
