.congra .container {
	max-width: 1900px;
	padding-top: 100px;
	padding-bottom: 100px;
}

.title-h2 {
	font-size: 48px;
	text-align: center;
}
.congra .title-h2 {
	margin-bottom: 55px;
}
.frame {
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
	z-index: 10;
	width: 100%;
	background-image: url("../img/i-br.png");
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
}
.frame__img {
	width: 96%;
	position: relative;
	z-index: 1;
}
.congra .swiper {
	width: 100%;
	height: 420px;
}
.congra .swiper-slide {
	height: 80%;
}
// swiper-pagination-bullet
// 	swiper-pagination-bullet-active
.slider .swiper-pagination-bullet {
	background: linear-gradient(180deg, #ffc500 0%, #ff9346 100%) !important;
	opacity: 1;
}

.slider .swiper-pagination-bullet-active {
	background: #612b1c !important;
}
.pagination {
	bottom: 30px !important;
}
.btn-prev.swiper-button-prev,
.btn-next.swiper-button-next {
	bottom: 8% !important;
	top: auto;
}
.btn-prev {
	left: 43%;
	right: auto;
}
.btn-next {
	right: 43%;
	left: auto;
}

.swiper-button-next::after,
.swiper-rtl .swiper-button-prev::after {
	content: "";
}
.swiper-button-prev::after,
.swiper-rtl .swiper-button-next::after {
	content: "";
}

.btn-prev,
.btn-next {
	width: 40px;
	height: 10px;
	margin: 0;
}
.btn-prev::after,
.btn-next::after {
	display: block;
	content: "";
	width: 100%;
	height: 100%;
	background-position: left center;
	background-size: cover;
	background-repeat: no-repeat;
}
.btn-prev::after {
	background-image: url(../img/prev.svg);
}
.btn-next::after {
	background-image: url(../img/next.svg);
}

@media (max-width: 1200px) {
	.congra .swiper-slide {
		height: 70%;
	}
	.btn-next {
		right: 40%;
		left: auto;
	}
	.btn-prev {
		left: 40%;
		right: auto;
	}
	.congra .container {
		padding-top: 40px;
		padding-bottom: 40px;
	}
}

@media (max-width: 768px) {
	.congra .swiper-slide {
		height: 80%;
	}
	.btn-prev {
		left: 35%;
		right: auto;
	}
	.btn-next {
		right: 35%;
		left: auto;
	}
}

@media (max-width: 576px) {
	.title {
		font-size: 36px;
	}
	.btn-prev {
		left: 30%;
		right: auto;
	}
	.btn-next {
		right: 30%;
		left: auto;
	}
}

@media (max-width: 380px) {
	.btn-next {
		right: 15%;
		left: auto;
	}
	.btn-prev {
		left: 15%;
		right: auto;
	}
}
