.teacher {
	padding-bottom: 100px;
}
.teacher .container {
	max-width: 953px;
}
.teacher .title-h2 {
	margin-bottom: 25px;
}
.subtitle {
	font-weight: 400;
	font-size: 16px;
	line-height: 1.2;
	text-align: center;
	color: #612b1c;
}

.slider-demo {
	width: 100%;
	margin-top: 50px;
	position: relative;
}

.slider__list {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.slider__list-item {
	margin: 0 15px;
	filter: drop-shadow(0px 20px 20px rgba(0, 0, 0, 0.1));
}

.slider__link {
	border: 10px solid #fff;
	transition: box-shadow 0.3s ease;
}

.slider__link:hover {
	box-shadow: 0px 0px 8px 3px rgba(34, 60, 80, 0.25);
}

.pagination-demo {
	margin-top: 40px;
	&__row {
		display: flex;
		align-items: center;
		justify-content: center;
	}
}
.pagination-demo__bullet {
	// flex: 0 0 auto;
	width: 10px;
	height: 10px;
	background-color: #ff9346;
	background: linear-gradient(180deg, #ffc500 0%, #ff9346 100%);
	border-radius: 50%;
	&:not(:last-child) {
		margin-right: 5px;
	}
}

.pagination-demo__bullet--action {
	background: #612b1c;
}
.navigations {
	position: absolute;
	bottom: 0;
	left: 50%;
	transform: translateX(-50%);
}
.navigations__prev {
	position: relative;
	left: -100px;
	bottom: -10px;
	display: block;
	content: "";
	width: 40px;
	height: 10px;
	background-position: left center;
	background-size: cover;
	background-repeat: no-repeat;
	background-image: url(../img/prev.svg);
}
.navigations__next {
	position: relative;
	right: -100px;
	bottom: 0px;
	display: block;
	content: "";
	width: 40px;
	height: 10px;
	background-position: left center;
	background-size: cover;
	background-repeat: no-repeat;
	background-image: url(../img/next.svg);
}
.upload-wrapper {
	margin-top: 60px;
}
.upload-container {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 294px;
	height: 62px;
	outline: none;
	background-color: #85371f;
	border: 2px solid transparent;
	border-radius: 60px;
	font-family: "Montserrat";
	font-size: 18px;
	text-align: center;
	color: #ffffff;
	margin: 0 auto;
	transition: all 0.3s ease;

	&:hover {
		border: 2px solid #85371f;
		background-color: #fff;
		color: #85371f;
	}
}

.upload-image {
	width: 20px;
	height: 20px;
	margin-right: 10px;
	fill: #fff;
	transition: fill 0.3s ease;
}

.upload-container label:hover {
	cursor: pointer;
	text-decoration: underline;
}

.upload-container div {
	position: relative;
	z-index: 10;
}

.upload-container input[type="file"] {
	width: 0.1px;
	height: 0.1px;
	opacity: 0;
	position: absolute;
	z-index: -10;
}

.upload-container input[type="file"]:focus + label {
	outline: 1px solid #0078d7;
	outline: -webkit-focus-ring-color auto 5px;
}

.upload-desc {
	font-weight: 400;
	font-size: 16px;
	line-height: 20px;
	text-align: center;
	color: #612b1c;
	margin-top: 22px;
}

.upload-container:hover .upload-image {
	fill: #612b1c;
}

@media (max-width: 998px) {
	.teacher {
		padding-bottom: 40px;
	}
}
