.worker {
	background-color: #fffbd8;
	padding-top: 150px;
	padding-bottom: 120px;
}
.worker .container {
	max-width: 1150px;
}
.worker__grid {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 30px 15px;
}

.user__info {
	display: flex;
}

.user__avatar {
	display: flex;
	align-items: center;
	justify-content: center;
	flex: 0 0 auto;
	width: 181px;
	height: 181px;
	position: relative;

	&::before {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		content: "";
		background-image: url("../img/bg-ava.png");
		background-position: left center;
		background-size: cover;
		background-repeat: no-repeat;
	}

	&::after {
		position: absolute;
		bottom: 0;
		left: 30px;
		width: 23px;
		height: 23px;
		content: "";
		background-image: url("../img/quota.svg");
		background-position: left center;
		background-size: cover;
		background-repeat: no-repeat;
	}

	img {
		width: 121px;
		height: 121px;
		position: relative;
		z-index: 1;
	}
}

.user__data {
	color: #612b1c;
	font-weight: 400;
	padding-top: 40px;
}

.user__name {
	font-family: RobotoSlab;
	font-size: 16px;
	margin-bottom: 15px;
	span {
		display: block;
	}
}
.user__position {
	font-size: 14px;
}

.user__text {
	font-weight: 400;
	font-size: 16px;
	color: #612b1c;
	padding-left: 60px;
	padding-top: 20px;
}

@media (max-width: 998px) {
	.worker__grid {
		grid-template-columns: repeat(2, 1fr);
	}
}

@media (max-width: 998px) {
	.worker__grid {
		grid-template-columns: repeat(1, 1fr);
	}
	.user__text {
		padding-left: 15px;
		padding-top: 15px;
	}
}
